export default {
	 name:"userInfo",
	data() {
		return {
			userInfo:{},
		}
	},
	onShow() {
		this.userInfo = uni.getStorageSync('userInfo');
	},
	mounted() {
		let userInfo1 = {
			name:'?',
			img:'../../static/img/avator.jpeg',
			code:'F15123754227',
			sex:'男',
			area:'中国大陆',
			text:'山高路远总有人为你而来'
		}
		uni.setStorageSync("userInfo",userInfo1);
		this.userInfo = uni.getStorageSync('userInfo');
	},
	methods: {
		goAvator(){
			uni.navigateTo({
				url:'../avator/avator'
			})
		},
		//调取系统相册
		avatorChange(){
			uni.chooseImage({
				count:1,
				sizeType:"original",
				success(res) {
					console.log(res);
					uni.previewImage({
						 // 对选中的图片进行预览
						urls: res.tempFilePaths,
					})
				}
			})
		},
		//修改昵称
		editName(){
			uni.navigateTo({
				url:'../editName/editName'
			})
		},
		//二维码名片
		goQrCode(){
			uni.navigateTo({
				url:'../qrCode/qrCode'
			})
		},
		//更多
		goMore(){
				uni.navigateTo({
					url:'../editMore/editMore'
				})
		},
		//地址
		goAddress(){
			uni.navigateTo({
				url:'../address/address'
			})
		}
	}
}
